.mechanism {
  background-size: cover;
  display: flex;
  align-items: center;
  transition-timing-function: ease-in-out;
  justify-content: center;
  position: absolute;
  &>.small-shestiryonka{
    width: 174px;
    height: 174px;
  }
  &>.medium-shestiryonka{
    width: 232px;
    height: 232px;
  }
  &>.big-shestiryonka{
    width: 330px;
    height: 330px;
  }
  &:nth-child(1) {
    top: 16%;
    left: 12%;
  }

  &:nth-child(2) {


    top: 17%;
    left: 27%;
  }

  &:nth-child(3) {
    left: 30%;
    top: 46%;
  }

  &:nth-child(4) {


    left: 7%;
    top: 42%;
  }

  &:nth-child(5) {
    top: 30%;
    left: 48%;
  }

  &:nth-child(6) {
    top: 56%;
    left: 61%;
  }

  &:nth-child(7) {

    top: 82%;
    left: 59%;

  }

  &:nth-child(8) {
    top: 89%;
    left: 76%;
  }

  &:nth-child(9) {

    top: 16%;
    left: 67%;
  }

  &:nth-child(10) {

    left: 21%;
    top: 75%;
  }

  &:nth-child(11) {
    left: 40%;
    top: 60%;

  }

  &:nth-child(12) {

    top: 53%;
    right: 9%;

  }

  &-text {
    width: 114px;
    height: 114px;
    color: white;
    position: absolute;
    border: 1px solid #032B96;
    font-family: 'Oxygen', sans-serif;
    border-radius: 100%;
    display: flex;
    align-items: center;
    cursor: pointer;
    justify-content: center;
    text-align: center;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 23px;
    letter-spacing: 0.03em;
    text-transform: uppercase;
  }
}

